.create-group-div {
  height: 50vh;
  display: grid;
}
.create-group-div .card {
  padding: 20px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  display: grid;
  width: 80%;
  height: 80%;
  grid-template-rows: 2fr 2fr 2fr 1fr;
}

.create-group-div .card h2 {
  text-align: center;
}
.create-group-div input {
  margin-left: 10px;
  width: 80%;
}
.create-group-div .card:hover {
  -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.create-group-div .btn-group {
  display: grid;
  grid-gap: 10px;
}



